@import url('https://fonts.googleapis.com/css2?family=Patua+One&family=Poppins:wght@400;500&family=Rubik:wght@300;400&display=swap');
@import url('https://fonts.googleapis.com/css2?family=Montserrat:wght@400;600&display=swap');

.full-screen{
  position: absolute;
  width: 100%;
  height: 100%;
  /* background-color: #27242d; */
  background: white;
  /* background:black; */
}

.scene{
  z-index: 1;
  width: 100%;
  height: 70%;
  top: 0%;
  left: 0%;
}

.full-app{
  z-index: 2;
  position: relative;
  width: 60%;
  margin: auto;  
}

.header-chunk{
  z-index: 2;
  position: relative;
  width: 80%;
  height: 30%;
  margin: auto;
}

.main-header{
  font-family: "Poppins";
  font-size: min(8vw,90px);
  width: 100%;
  color: #888789;
}

.header-style{
  margin-top: 50px;
  margin-bottom: 0px;
  text-align: center
}

.sub-header{
  font-family: 'Poppins', sans-serif;
  font-weight: 500;
  font-size: min(4vw,45px);
  /* width: 600px; */
  margin: auto;
  color: #888789;
  /* border: solid blue; */
}

.sub-header-ff{
  font-family: 'Poppins', sans-serif;
  font-weight: 500;
  font-size: 30px;
  /* width: 600px; */
  margin: auto;
  color: #888789;
}

.elevator-levels{
  z-index: 3;
  position: fixed;
  top: 50%;
  left: 2%;
  transform: translate(0%, -50%);
  width: 20px; 
}

button{
  margin-top: 10px;
  margin-bottom: 10px;
  border-radius: 5px;
  border: none;
  width: 20px;
  height: 10px; 
}

.button-offfocus{ 
  background: rgb(99, 87, 122);
}

.button-onfocus{
  background: rgb(165, 144, 202);
}

.infotext-dark{
  color: gray;
  margin-top: 0px;
  margin-bottom: 0px;
}

.title{
  position: absolute;
}

.bio{
  /* border: solid black; */
}

.center{
  width: 100%;
  display: flex;
  justify-content: center;
}

.paper-image{
  width: 200px;
  height: 200px;
  border-radius: 30px;
  margin-top: 15px;
  margin-left: 10px;
  margin-right: 10px;
}

.work-block{
  display: flex;
  flex-wrap: wrap;
  justify-content: center;
  margin-top: 15px;
  margin-bottom: 15px;
  margin: auto;
}

.paper-text{
  width: 80%;
}

.web-logo{
  width: 50px;
}

.navbar{
  display: flex;
  width: 100%;
  margin-top: 30px;
}

.my-name{
  width: 30%;
  margin-top: auto;
  margin-bottom: auto;
  font-family: 'Poppins', sans-serif;
  margin-left: 15px;
  font-weight: 800;
  font-size: 30px;
}

.section-tabs{
  width: 80%;
  font-family: 'Poppins', sans-serif;
  font-weight: 500;
  margin-top: auto;
  text-align: right;
  margin-bottom: auto;
}

.research-block{
  width: 60%;
  margin-left: auto;
  margin-right: auto;
  margin-top: 50px;
}


.work-title{
  font-family: 'Montserrat';
  font-weight: 600;
}

.work-description{
  font-family: 'Montserrat';
  font-weight: 400;
}


footer{
  font-family: 'MontSerrat';
  font-weight: 400;
  font-size: 14px;
  color: #888789;
  text-align: center;
  margin-top: 40px;
  margin-bottom: 40px;
}

.about-block{
  margin-left: auto;
  margin-right: auto;
  margin-top: 50px;
}

.intro-block{
  margin-top: 15px;
  margin-bottom: 15px;
  margin: auto;
  width: 60%;
}

.intro-image{
  width: 200px; 
  border-radius: 50%;
  margin-left: 50%;
  transform: translate(-50%, 0%);
  margin-top: 30px;
  margin-bottom: 30px;
}

.intro-description{
  font-family: 'Montserrat';
  font-weight: 400;
}

.intro-text{
  margin: auto;
  width: 80%;
}

.spacer{ 
  color:  #88878955;
}
